<script>
import Swiper from "swiper" 
import 'swiper/dist/css/swiper.css'
export default {
    data() {
        return {
            
        }
    },
    methods: {
        swiper() {
            var mySwiper = new Swiper('.swiper-container', {
                loop: true, // 
                effect: 'slide',
                autoplay:{
                    delay:5000
                },
                pagination: {
                    el: '.swiper-pagination',
                    clickable:true,
                    type:'bullets',
                },
                spaceBetween:10,
                centeredSlides:true,
                slidesPerView:1.36
            })
            $(".swiper-pagination-bullet").hover(()=> {
                $(this).click(); //
            },()=> {
                mySwiper.autoplay.start(); //
            })
        }
    },
    mounted() {
        this.swiper();
    }
}
</script>